import moment from 'moment';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Alert, Col, CustomInput, Form, FormGroup, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  dispatchClose,
  EditViewProps,
  ERROR,
  InputGroupInline,
  InputType,
  isEmpty,
  ProcessBar,
  showToast,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { RaisParam } from '../../../../models/dtos/mfp/raisParam';
import { NAO_RETIFICADO, RETIFICACAO_ENUM_VALUES, RETIFICADO } from '../../../../models/enumerated/mfp/retificacaoEnum';
import { gerarRais } from '../../../../services/mfp.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState, toggleHeaderDrawer } from '../../../../store/ducks/global.duck';
import EmpresaIS from '../../../components/ger/empresaIS/EmpresaIS';
import ResponsavelIS from '../../../components/ger/responsavelIS/ResponsavelIS';

interface ExportacaoRaisProcessViewProps extends EditViewProps<RaisParam> {}

const ExportacaoRaisProcessView: FC<ExportacaoRaisProcessViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { ...formProps } = useForm<RaisParam>({ mode: 'onBlur' });
  const { headerDrawerToggle } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const dispatch = useDispatch();

  const [isEnabledtRetificacao, setIsEnabledtRetificacao] = useState(false);
  const [isEnableCrea, setIsEnableCrea] = useState(false);
  const [creaDtRetificacaoIsRequired, setCreaDtRetificacaoIsRequired] = useState(false);

  const handleOnchangeRetificacao = () => {
    const retificacao = formProps.getValues().retificacao;
    if (retificacao === NAO_RETIFICADO.value) {
      setIsEnabledtRetificacao(true);
      setIsEnableCrea(true);
      if (isEmpty(formProps.getValues().crea) && isEmpty(formProps.getValues().dtRetificacao)) {
        setCreaDtRetificacaoIsRequired(false);
      }
    } else if (retificacao === RETIFICADO.value) {
      setIsEnabledtRetificacao(false);
      setIsEnableCrea(false);
      setCreaDtRetificacaoIsRequired(true);
    }
  };

  useEffect(
    () => {
      const { usuario } = globalReducer.globalParameter;
      let ano;
      if (globalReducer.globalParameter.empresa !== null && globalReducer.globalParameter.empresaMFP !== null) {
        ano = moment(globalReducer.globalParameter.empresaMFP.referencia, 'YYYY-MM');
      }

      const params: AlertProps = {
        message:
          'Atenção: Antes de gerar o arquivo, verifique se as acumulações dos eventos para a RAIS e se as informações cadastrais dos vínculos estão corretas. \n' +
          'A exatidão das informacões é de responsabilidade do usuário do sistema.',
        type: AlertType.INFORMATION,
      };
      alertManager.emit(ACTION_EVENTS.SHOW, params);

      setTimeout(() => {
        formProps.setValue('ano', Number(ano.format('YYYY')) - 1, true);
        formProps.setValue('retificacao', NAO_RETIFICADO.value, true);
        formProps.setValue('usuario.id', usuario.id, true);
      }, 500);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onClose = (goBack: boolean = true) => {
    dispatchClose(props, props.history, goBack);
  };

  const handleCancel = () => onClose();

  function callExport(data: RaisParam): void {
    gerarRais(data, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
      },
      thenFunction: () => {
        if (!headerDrawerToggle) {
          dispatch(toggleHeaderDrawer());
        }
      },
    });
  }

  return (
    <WindowComponent
      title="Exportação - RAIS"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
      wikiId={22}
    >
      <Form>
        <FormGroup>
          <InputGroupInline formProps={formProps} id="usuario.id" hidden={true} />
          <Row>
            <Col>
              <Alert color="danger" className="text-center">
                É de extrema importância que os dados sejam conferidos no validador da RAIS.
              </Alert>
            </Col>
          </Row>
          <EmpresaIS
            id="empresas"
            formProps={{ ...formProps, validation: { required: true } }}
            inputSearchProps={{ selectProps: { isMulti: true }, validation: { required: true } }}
            placeholder="Selecione uma ou várias empresas."
            isPrependLabel={true}
            label="Empresa(s)"
          />
          <ResponsavelIS
            id="responsavel"
            placeholder="Informe o Responsável"
            formProps={{ ...formProps, validation: { required: true } }}
            label="Responsável"
            isPrependLabel={true}
          />
          <InputGroupInline
            id="retificacao"
            label="Retificação"
            isPrependLabel={true}
            placeholder="Informe a Retificação"
            formProps={formProps}
            inputDropDownProps={{
              optionsProvider: RETIFICACAO_ENUM_VALUES,
            }}
            type={InputType.DROP_DOWN}
            onChange={handleOnchangeRetificacao}
          />
          <InputGroupInline
            disabled={isEnabledtRetificacao}
            id="dtRetificacao"
            formProps={{ ...formProps, validation: { required: creaDtRetificacaoIsRequired } }}
            isPrependLabel={true}
            placeholder="Informe a Data de Retificação"
            label="Data de Retificação"
            type={InputType.DATE}
          />
          <InputGroupInline
            disabled={isEnableCrea}
            placeholder="Informe o CREA"
            id="crea"
            formProps={{ ...formProps, validation: { required: creaDtRetificacaoIsRequired } }}
            isPrependLabel={true}
            label="CREA"
          />
          <Row>
            <Col>
              <InputGroupInline
                id="ano"
                formProps={{ ...formProps, validation: { min: 1930, max: 3000 } }}
                isPrependLabel={true}
                label="Ano Base"
                placeholder="Informe o Ano Base"
                type={InputType.NUMBER}
              />
            </Col>
            <Col>
              <CustomInput
                type="switch"
                id="gerarPorTomador"
                name="gerarPorTomador"
                label="Gerar com os dados dos tomadores"
                innerRef={formProps.register}
              />
            </Col>
          </Row>
        </FormGroup>
        <ProcessBar handleSubmit={formProps.handleSubmit(callExport)} handleClose={handleCancel} />
      </Form>
    </WindowComponent>
  );
};

export default ExportacaoRaisProcessView;
